import Vue from 'vue'
import Request from '../utils/luch-request/request'
import Store from "../store";
import AppConfig from './config'
const http = new Request();

http.setConfig((config) => { /*  */
    config.baseUrl = AppConfig.api_url; /*  */
    config.header = {
		"Content-Type": "application/x-www-form-urlencoded",
	}
    return config
})

http.interceptor.request((config, cancel) => { /*  */
    config.header = {
        Authorization:'Bearer '+Store.getters.jwt,
        ...config.header,
    }
    /*
    if (!token) { // tokencancel catch() 
        cancel('token ') // catch((err) => {}) err.errMsg === 'token '
    }
    */
    return config;
})

http.interceptor.response((response) => { /*  */
    // console.log("--- http response " + response.config.url + " ---", response.data);

    switch(response.statusCode){
        case 200:
			if(response.data.code == '21'){
				uni.showModal({
				    title: '',
				    content: '',
					showCancel:false,
					confirmText:'',
					confirmColor:'#007AFF',
				    success: function (res) {
				        if (res.confirm) {
				            console.log('');
							// http.get('personwx.refreshJWT/1.0/',{
							// 	openId:Store.getters.openId
							// }).then(res =>{
							// 	console.log(res)
							// })
							uni.showLoading({
							    title: ''
							});
							uni.request({
							    url: AppConfig.api_url+'personwx.refreshJWT/1.0/', //
							    data: {
							        openId:Store.getters.openId
							    },
								method:'GET',
							    header: {
							        "Content-Type": "application/x-www-form-urlencoded", //
							    },
							    success: (res) => {
							        console.log(res.data);
									let uinfo = Store.state.user_info
									uinfo.id = res.data.data.id
									uinfo.jwt = res.data.data.jwt
									uinfo.openid = res.data.data.openId
									uinfo.loginName = res.data.data.loginName
									Store.commit('setUserInfo',uinfo)
									uni.hideLoading();
									uni.showToast({
									    title: '',
									    duration: 2000,
										icon:'none'
									}); 
									uni.navigateBack({
									    delta: 1
									});
							    }
							});
				        }
				    }
				});
				return
			}
            break;

        case 400:
            uni.showToast({
                title: '~',
                icon: 'none',
            })

            // Store.commit('loginSuccess', {})
            setTimeout (() => {
                // uni.navigateTo({
                //     url:'/pages/auth/login?scene=back',
                // })

                // uni.navigateTo({
                //     url: '/pages/index/index?page=user'
                // });
            }, 1500)

            break;

        case 410:
            break;

        case 412:
            uni.showToast({
                title: response.data.errmsg,
                icon: 'none',
            })
            break;


        case 404:
        case 500:
            uni.showToast({
                title: '~',
                icon: 'none',
            })
            break;
        default:
            uni.showToast({
                title: '~',
                icon: 'none',
            })
            break;
    }

    return response;
})

export default http


